@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:"Baloo Paaji 2", monospace}input[type="color"]{-webkit-appearance:none;border:none;width:3rem;height:1.5rem}input[type="color"]::-webkit-color-swatch-wrapper{padding:0}input[type="color"]::-webkit-color-swatch{border:none;border-radius:0.3rem}input[type="number"]{width:100%;height:1.3rem;background-color:transparent;border:none;text-align:center;border-bottom:2px solid #00445e}input[type="number"]:focus{background-color:#00435e2d}button{padding:0 0.5rem;margin-right:0.3rem;border-radius:0.3rem;border:2px solid #00445e;color:#00445e;font-weight:bold;cursor:pointer}label{margin-right:0.5rem}.disable-btn{color:gray;border-color:gray}body{background:#00445e}body header{max-width:1024px;margin:0 auto;padding:1rem 1rem}body header .header-container{display:flex;justify-content:space-between;align-items:center;color:#c5c5c5}body header .header-container h1{font-size:2.2rem}body header .header-container .options{list-style:none;display:flex}body header .header-container .options li{padding:0.4rem;background-color:#c5c5c5;border:1px solid #00445e;display:flex;border-radius:50%;margin-left:0.4rem;position:relative;cursor:pointer}body header .header-container .options li:before{content:"";position:absolute;width:100%;height:100%;top:0;left:0;background-color:#00445e;border-radius:50%;transition:all 0.2s linear;-webkit-transform:scale(0);transform:scale(0);opacity:0}body header .header-container .options li:hover:before{-webkit-transform:scale(1);transform:scale(1);opacity:1;border:1px solid #c5c5c5}body header .header-container .options li:hover a{color:#c5c5c5}body header .header-container .options li a{color:#00445e;font-size:1.2rem;display:flex;justify-content:center;align-items:center;transition:all 0.2s linear;z-index:0}body main{max-width:1024px;margin:0 auto;padding:0 1rem}body main .information{background-color:rgba(0,255,255,0.274);border-radius:0.3rem 0.3rem 0 0;width:100%}body main .information ul{list-style:none;padding:0.5rem 0 0 0.5rem;display:flex}body main .information ul li{margin-right:1rem;color:whitesmoke}body main .container{width:100%;height:30rem;border-radius:0 0 0.3rem 0.3rem;background-color:rgba(0,255,255,0.274);padding:0.5rem}body main .container .game-wrapper{width:100%;height:100%;overflow-y:scroll;overflow-x:scroll}body footer{max-width:1024px;margin:0 auto;padding:1rem 1rem}body footer .footer-container{display:flex;justify-content:center;align-items:center;flex-direction:column;color:#c5c5c5}body footer .footer-container h3{margin-bottom:0.5rem}body footer .footer-container .options{list-style:none;display:flex;justify-content:center;align-items:center}body footer .footer-container .options a{margin:0 0.3rem;color:#c5c5c5;display:flex;justify-content:center;align-items:center;font-size:1.8rem;cursor:pointer;transition:0.5s all linear}body footer .footer-container .options a:hover{-webkit-transform:rotate(360deg) scale(0.8);transform:rotate(360deg) scale(0.8)}body .config{max-width:500px;width:90%;position:fixed;top:50%;left:50%;-webkit-transform:translateY(-50%) translateX(-50%);transform:translateY(-50%) translateX(-50%);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);background-color:rgba(255,255,255,0.815);border-radius:0.3rem;padding:0.5rem;box-shadow:0px 25px 50px rgba(0,0,0,0.1),0 0 0 1000px rgba(15,15,15,0.95)}body .config .config-wrapper{border-radius:0.3rem;border:3px solid #00445e;padding:1rem}body .config .config-wrapper .config-close{display:flex;justify-content:space-between;padding:0 0.5rem 0.5rem 0;border-bottom:2px solid #00445e;margin-bottom:1rem}body .config .config-wrapper .config-close .close{font-size:1.5rem}body .config .config-wrapper .config-close .close:hover{color:#00445e}body .config .config-wrapper .config-row{display:flex;align-items:center;margin-bottom:0.5rem}body .config .config-wrapper .config-row .input-type{display:flex;justify-content:space-between;margin-right:1rem}body .config .config-wrapper .config-row .config-btn-ant{padding:0.1rem;display:flex;justify-content:center;align-items:center;margin-right:1rem}body .config .config-wrapper .config-row .config-btn-ant .config-ant{font-size:1.2rem;background-color:#ffffff;color:black}body .config .config-wrapper hr{margin:1rem 0 0.5rem 0}body .config .config-wrapper p{font-weight:bold}body .config .config-wrapper .config-add{display:flex;align-items:center;justify-content:center;width:100%;font-size:0.9rem}@media (max-width: 690px){.header-container{flex-direction:column}}

